body {
  margin: 0;
  padding: 0%;
}
:root{
  font-family: "Britannic";
}
::placeholder{
  color: rgb(127, 114, 162) ;
}
header{
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgb(12, 0, 42);
  color: rgb(127, 114, 162);;
  font-weight: bold;
  font-family: "Britannic";
}

.left {
  padding-left: 20px;
  cursor: pointer;
  font-weight: 900;
  color:rgb(127, 114, 162) ;
}

.middle ul {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: center;
}

.middle li {
  padding: 0px 20px;
  letter-spacing: 1.5px;
  cursor: pointer;
}
.middle li:hover{
  color:rgb(58, 252, 255) ;
}
.right{
  display: flex;
  /* width: 210px; */
}

.searchbar {
  margin-right: 20px;
  border: 1px solid rgb(127, 114, 162);
  border-radius: 5px;
  width: 120px;
}
.menubtn svg{
  fill:rgb(127, 114, 162);
  cursor: pointer;
  margin-right: 20px;
  display: none;
}

#search {
  padding: 7px 5px;
  width: 100%;
  outline: none;
  border: none;
  font-family: "Britannic";
  font-size: 16px;
  background-color: transparent;
  color: rgb(127, 114, 162);;
  /* border-right: 1px solid rgb(235, 219, 0); */
  caret-color: rgb(58, 252, 255);
}



#search_btn {
  padding: 7px 5px;
  width: 30%;
  background-color: transparent;
  outline: none;
  border: none;
  color: rgb(235, 219, 0);
  font-weight: bold;
  font-family: "Copperplate Gothic";
  cursor: pointer;
}







.section1{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  color: rgb(12, 0, 42);
}
#textarea{
  width: 65%;
  padding: 20px;
  font-size: 14px;
  outline: none;
  border-radius: 5px;
  background-color: rgb(127, 114, 162);
  border: 1px solid rgb(12, 0, 42);
  caret-color: rgb(12, 0, 42);
  color: rgb(12, 0, 42);
}
.edit_buttons{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 70%;
}
.button{
  background-color:rgb(12, 0, 42);
  color: rgb(127, 114, 162);
  font-weight: bold;
  font-size: 18px;
  padding: 7px 20px;
  border-radius: 5px;
  margin: 10px;
  cursor: pointer;
}
.button:active{
  background-color: rgb(127, 114, 162);
  border: 1px solid rgb(12, 0, 42);
  color: rgb(12, 0, 42);
}
.button:hover{
  animation: active_button 0.1s;
}
@keyframes active_button{
  0%{
    transform: scale(0.9);
  }
  20%{
    transform: scale(1);
    /* transition: 1s; */
  }
  40%{
    transform: scale(1.1);
    /* transition: 1s; */
  }
  60%{
    transform: scale(1);
    /* transition: 1s; */
  }
  80%{
    transform: scale(0.9);
  }
  100%{
    transform: scale(1);
  }
  
}

.other_details{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 70%;
  font-weight: 500;
  color: rgb(12, 0, 42);
}
.other_details span:not(:last-child){
  padding: 10px 10px;
}









@media screen and (min-width: 700px) {
  .left{
      display: block !important;
      opacity: 1;
      width: auto;
  }
}

@media screen and (max-width: 699.99px) {
  .header1{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .left{
      
  }
  .right{
      /* display: none; */
  }
  .middle{
      display: flex;
      justify-content: center;
      align-items: center;
      display: none;
  }
  .middle ul {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      /* display: none; */
      margin-top: 50px;
      
  }
  .middle li{
      margin: 7px 0px;
  }
  .right{
      width: auto;
      align-items: center;
  }
  .searchbar{
      margin-top: 5px;
      margin-bottom: 5px;
      width: 120px;

  }
  #search{
      width: 100px;
  }
  #search_btn{
      width: 50px;
  }
  .menubtn svg{
      display: block;
  }
  
}
@media screen and (min-width: 321px) {
  .left{
      display: block !important;
  }
}
@media screen and (max-width: 320px) {
  .left{
      display: none !important;
  }
  .right{
      width: 100%;
      justify-content: space-between;
  }
}

@media screen and (max-height: 590px) {
  form{
      margin-top: 10vh;
  }
  #subject {
      margin-top: 10vh;
  }
}